import React, { useState } from 'react'

import { faHome, faCubes, faCube } from '@fortawesome/free-solid-svg-icons'

import AppContext from '@src-contexts/app-context'
import SEO from '@src-components/common/seo'

import Layout from '@src-components/layout'

import PageHeading from '@src-components/parts/page-heading'

import TaxonomyList, {
	TaxonomyListItem,
} from '@src-components/parts/taxonomy-list'

import { AllCategoryContentQuery, AllCategoriesGroupQuery } from '@src-queries'

const CategoriesListTemplate = props => {
	const { location } = props

	const allCategoryContentData = AllCategoryContentQuery()
	const allCategoriesGroupData = AllCategoriesGroupQuery()

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)

	const pageTitle = 'カテゴリー一覧'
	const pageDescription = ''

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: pageTitle, url: location.pathname, icon: faCubes },
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faCubes}>{pageTitle}</PageHeading>
				<section className="mt-8">
					<TaxonomyList>
						{allCategoriesGroupData.map(categoriesGroupData => {
							const findCategoryData = allCategoryContentData.find(
								categoryContentData =>
									categoryContentData.node.fields.name ===
									categoriesGroupData.fieldValue
							)
							return (
								<TaxonomyListItem
									key={findCategoryData.node.id}
									item={findCategoryData}
									total={categoriesGroupData.totalCount}
									icon={faCube}
								/>
							)
						})}
					</TaxonomyList>
				</section>
			</Layout>
		</AppContext.Provider>
	)
}

export default CategoriesListTemplate
